<template>
    <div class="content-box">
        <div class="content-wrap">
            <div class="content-dimesion atom-statistics" v-bkloading="{isLoading: isCitationLoading, opacity: 1}">
                <div class="clearfix">
                    <div class="content-title">{{i18n.numberCitations}}</div>
                    <div class="content-date">
                        <div class="content-date-business">
                            <bk-dropdown-menu ref="dropdown" trigger="click">
                                <span slot="dropdown-trigger">
                                    <span class="dropdown-cursor">{{choiceBusinessName? choiceBusinessName : i18n.chooseBusiness}}</span>
                                    <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
                                </span>
                                <ul class="bk-dropdown-list" slot="dropdown-content">
                                    <li v-for="item in businessList" :key="item.cc_id">
                                        <a href="javascript:void(0);" @click="onAtomCiteData(item.cc_id, item.cc_name)"> {{item.cc_name}}</a>
                                    </li>
                                </ul>
                            </bk-dropdown-menu>
                        </div>
                        <div class="content-date-picker" @click="onDatePickerClick">
                            <el-date-picker
                                ref="datePickerRef"
                                v-model="time"
                                align="center"
                                type="daterange"
                                format="yyyy-MM-dd"
                                value-format="timestamp"
                                :picker-options="pickerOptions"
                                :clearable="false"
                                :editable="false"
                                @change="onAtomCiteData()">
                            </el-date-picker>
                            <i :class="['bk-icon icon-angle-down', {'icon-flip': chooseDownShow}]"></i>
                        </div>
                    </div>
                </div>
                <data-statistics :dimensionList="taskPlotData" :totalValue="taskTotal"></data-statistics>
            </div>
        </div>
        <div class="content-process-detail">
            <bk-tab :type="'fill'" :active-name="tabName" @tab-changed="onChangeTabPanel">
                <bk-tabpanel name="processDetails" :title="i18n.processDetail">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.atom}}</label>
                                <el-select v-model="atom" filterable :placeholder="i18n.choice" @change="onAtomTemplateData">
                                    <el-option
                                        v-for="item in components"
                                        :key="item.code"
                                        :label="item.group_name +'-'+ item.name"
                                        :value="item.code">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.taskStartTime}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onAtomTemplateData">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomTemplateData">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomTemplateData">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isDetailLoading, opacity: 1}"
                            :data="templateData"
                            :total="templateTotal"
                            :options="dataTableOptions"
                            :pagination="templatePagination"
                            :columns="templateColumns"
                            @handleSizeChange="onTemplateHandleSizeChange"
                            @handleIndexChange="onTemplateHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
                <bk-tabpanel name="executionTime" :title="i18n.executionTime">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.taskStartTime}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onAtomExecuteData">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomExecuteData">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomExecuteData">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isExecutionLoading, opacity: 1}"
                            :data="executeData"
                            :total="executeTotal"
                            :columns="executeColumns"
                            :pagination="executePagination"
                            @handleSizeChange="onExecuteHandleSizeChange"
                            @handleIndexChange="onExecuteHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
                <bk-tabpanel name="taskDetails" :title="i18n.taskDetail">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.atom}}</label>
                                <el-select v-model="atom" filterable :placeholder="i18n.choice" @change="onAtomInstanceData">
                                    <el-option
                                        v-for="item in components"
                                        :key="item.code"
                                        :label="item.group_name +'-'+ item.name"
                                        :value="item.code">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.taskStartTime}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM- dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onAtomInstanceData">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomInstanceData">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onAtomInstanceData">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isTaskLoading, opacity: 1}"
                            :data="instanceData"
                            :total="instanceTotal"
                            :options="dataTableOptions"
                            :pagination="instancePagination"
                            :columns="instanceColumns"
                            @handleSizeChange="onInstanceHandleSizeChange"
                            @handleIndexChange="onInstanceHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
            </bk-tab>
        </div>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import tools from '@/utils/tools.js'
import DataStatistics from '../dataStatistics/index.vue'
import {mapActions, mapState} from 'vuex'
import {AnalysisMixins} from '@/mixins/js/analysisMixins.js'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import { errorHandler } from '@/utils/errorHandler.js'
export default {
    name: 'StatisticsAtom',
    components: {
        DataStatistics,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                numberCitations: gettext('引用次数'),
                processDetail: gettext('流程详情'),
                executionTime: gettext('执行耗时'),
                taskDetail: gettext('任务详情'),
                timeLimit: gettext('时间范围'),
                taskStartTime: gettext('任务开始时间'),
                chooseCategory: gettext('选择分类'),
                chooseBusiness: gettext('选择业务'),
                chooseTime: gettext('选择时间'),
                choice: gettext('请选择'),
                atom: gettext('原子')
            },
            bizCcId: '',
            category: '',
            choiceBusinessName: '',
            isDropdownShow: false,
            chooseDownShow: false,
            datePickerRefShow: false,
            isDetailLoading: true,
            isCitationLoading: true,
            isExecutionLoading: true,
            isTaskLoading: true,
            time: [0, 0],
            taskPlotData: [],
            nodeData: [],
            templateData: [],
            templateTotal: 0,
            templatePageIndex: 1,
            templateLimit: 15,
            templatePagination: {
                limit: this.templateLimit,
                pageIndex: this.templatePageIndex,
                pageArray: this.dataTablePageArray
            },
            templateColumns: [
                {
                    prop: 'templateName', // 识别id
                    label: gettext('流程名称')// 表头显示名称
                },
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center'// 对其格式，可选（right，left，center）
                },
                {
                    prop: 'editTime',
                    label: gettext('更新时间'),
                    align: 'center'
                },
                {
                    prop: 'editor',
                    label: gettext('更新人'),
                    align: 'center',
                    formatter: (row, column, cellValue) => {
                        return `<span>${row.editor || '--'}</span>`
                    }
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                }
            ],
            tabName: 'processDetails',
            nodePagination: {
                // 分页操作
                limit: this.nodeLimit,
                pageIndex: this.nodePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            nodeColumns: [
                {
                    prop: 'instanceName',
                    label: gettext('任务名称')
                },
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center' // 对其格式，可选（right，left，center）
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center'
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                },
                {
                    prop: 'atomTotal',
                    label: gettext('原子个数'),
                    align: 'center'
                },
                {
                    prop: 'subprocessTotal',
                    label: gettext('子流程个数'),
                    align: 'center'
                },
                {
                    prop: 'gatewaysTotal',
                    label: gettext('网关个数'),
                    align: 'center'
                }
            ],
            atom: '',
            components: [],
            executeData: [],
            executeTotal: 0,
            executePageIndex: 1,
            executeLimit: 15,
            executePagination: {
                limit: this.executeLimit,
                pageIndex: this.executePageIndex,
                pageArray: this.dataTablePageArray
            },
            executeColumns: [
                {
                    prop: 'componentName',
                    label: gettext('原子名称')
                },
                {
                    prop: 'executeTimes',
                    label: gettext('执行次数'),
                    align: 'center'
                },
                {
                    prop: 'failedTimes',
                    label: gettext('失败次数'),
                    align: 'center'
                },
                {
                    prop: 'avgExecuteTime',
                    label: gettext('平均执行耗时（秒）'),
                    align: 'center'
                },
                {
                    prop: 'failedTimesPercent',
                    label: gettext('失败率'),
                    align: 'center'
                }
            ],
            instanceData: [],
            instanceTotal: 0,
            taskTotal: 0,
            instancePageIndex: 1,
            instanceLimit: 15,
            instancePagination: {
                limit: this.instanceLimit,
                pageIndex: this.instancePageIndex,
                pageArray: this.dataTablePageArray
            },
            instanceColumns: [
                {
                    prop: 'instanceName',
                    label: gettext('任务名称')
                },
                {
                    prop: 'businessName',
                    label: gettext('所属业务'),
                    align: 'center'
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center'
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                }
            ]
        }
    },
    created () {
        this.getDateTime()
    },
    mounted () {
        this.onAtomCiteData()
        if (this.categorys.length === 0) {
            this.getCategorys()
        }
        if (this.components.length === 0) {
            this.getComponentList()
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        }),
        businessList () {
            if (this.bizList.length === 0) {
                this.getBizList()
            }
            const list = tools.deepClone(this.bizList)
            list.unshift({cc_id: undefined, cc_name: gettext('全部')})
            return list
        }
    },
    methods: {
        ...mapActions('atomList/', [
            'queryAtomData',
            'loadSingleAtomList'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        onTemplateHandleSizeChange (limit) {
            this.templatePageIndex = 1
            this.templateLimit = limit
            this.onAtomTemplateData()
        },
        onTemplateHandleIndexChange (pageIndex) {
            this.templatePageIndex = pageIndex
            this.onAtomTemplateData()
        },
        onExecuteHandleSizeChange (limit) {
            this.executePageIndex = 1
            this.executeLimit = limit
            this.onAtomExecuteData()
        },
        onExecuteHandleIndexChange (pageIndex) {
            this.executePageIndex = pageIndex
            this.onAtomExecuteData()
        },
        onInstanceHandleSizeChange (limit) {
            this.instancePageIndex = 1
            this.instanceLimit = limit
            this.onAtomInstanceData()
        },
        onInstanceHandleIndexChange (pageIndex) {
            this.instancePageIndex = pageIndex
            this.onAtomInstanceData()
        },
        onAtomCiteData (business, name) {
            if (!Array.isArray(name)) {
                this.choiceBusinessName = name
            }
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'atom_cite',
                conditions: JSON.stringify({
                    create_time: time[0],
                    biz_cc_id: business,
                    finish_time: time[1]
                })
            }
            this.atomData(data)
        },
        onAtomTemplateData () {
            this.isDetailLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'atom_template',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        component_code: this.atom
                    }),
                    pageIndex: this.templatePageIndex,
                    limit: this.templateLimit
                }
                this.atomTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isDetailLoading = false
            }
        },
        onAtomExecuteData () {
            this.isExecutionLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'atom_execute',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category
                    }),
                    pageIndex: this.executePageIndex,
                    limit: this.executeLimit
                }
                this.atomTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isExecutionLoading = false
            }
        },
        async atomTableData (data) {
            try {
                const templateData = await this.queryAtomData(data)
                switch (data.group_by) {
                    case 'atom_template':
                        this.templateData = templateData.data.groups
                        this.templateTotal = templateData.data.total
                        break
                    case 'atom_execute':
                        this.executeData = templateData.data.groups
                        this.executeTotal = templateData.data.total
                        break
                    case 'atom_instance':
                        this.instanceData = templateData.data.groups
                        this.instanceTotal = templateData.data.total
                        break
                }
            } catch (e) {
                errorHandler(e, this)
            }
        },
        async atomData (data) {
            this.isCitationLoading = true
            try  {
                const templateData = await this.queryAtomData(data)
                this.taskPlotData = templateData.data.groups
                this.taskTotal = templateData.data.total
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isCitationLoading = false
            }
        },
        onAtomInstanceData () {
            this.isTaskLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'atom_instance',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        component_code: this.atom
                    }),
                    pageIndex: this.instancePageIndex,
                    limit: this.instanceLimit
                }
                this.atomTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isTaskLoading = false
            }
        },
        async getComponentList () {
            try {
                this.components = await this.loadSingleAtomList()
                this.atom = this.components[0].code
                this.onAtomTemplateData()
            } catch (e) {
                errorHandler(e, this)
            }
        },
        getDateTime () {
            let date = new Date()
            date.setHours(0, 0, 0)
            this.time[1] = Date.parse(date)
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
            this.time[0] = Date.parse(date)
        },
        onChangeTabPanel (name) {
            this.tabName = name
            switch (name) {
                case 'processDetails' :
                    this.onAtomTemplateData()
                    break
                case 'executionTime' :
                    this.onAtomExecuteData()
                    break
                case 'taskDetails':
                    this.onAtomInstanceData()
                    break
            }
        },
        onDatePickerClick () {
            this.datePickerRefShow = !this.datePickerRefShow
            this.$refs.datePickerRef.pickerVisible = this.datePickerRefShow
        }
    }
}
</script>

<style lang="scss">
.content-box {
    .content-wrap {
        .content-dimesion.atom-statistics {
            width: 100%;
            .chart-statistics-tool .tool-name {
                width: 250px;
            }
        }
    }
}
</style>
